import util from '@/common/util.js'
import graceChecker from '@/common/graceChecker.js'
export default {
	data() {
		return {
			index: -1,
			sex: ['男', '女'],
			date: '1990-01-01',
			phone: '',
			captcha: '',
			captchaFlag: true,
			captchaName: "获取验证码",
			captchaTime: 60,
			password: "123456",
			confirmPassword: "123456",
			isFocus: false,
			loginIp: ''
		};
	},
	onLoad(e) {
		let _this = this;
		// 获取客户端IP
		_this.loginIp = util.getClientIp()
	},
	methods: {
		sexChange(e) {
			this.index = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		// 获取验证码
		getCaptcha() {
			let _this = this;
			if (_this.captchaFlag) {
				_this.captcha = '123456';
				_this.captchaFlag = false;
				var interval = setInterval(() => {--_this.captchaTime;}, 1000)
				setTimeout(() => {
					clearInterval(interval)
					_this.captchaFlag = true
					_this.captchaTime = 60
				}, 60000)
			}
		},
		change() {
			let _this = this;
			// 注册信息校验
			var captchaRule = [
				{
					name: 'captcha',
					checkType: 'notnull',
					errorMsg: '请输入验证码'
				},
				{
					name: 'captcha',
					checkType: 'string',
					checkRule: '6',
					errorMsg: '验证码为 6 个数字'
				}
			];
			var teletphoneRule = [
				{
					name: 'phone',
					checkType: 'phoneno',
					checkRule: '',
					errorMsg: '手机号格式不正确'
				}
			];
			//进行表单检查
			var checkRes = graceChecker.check(_this, loginRule);
			if (checkRes) {
				uni.showToast({
					title: "验证通过!",
					icon: "none"
				});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
				_this.isFocus = true;
				return;
			}
			uni.showLoading({
				title: '注册中...'
			});
			// 个人手机号注册用户
			uni.request({
				url: _this.EnsembleUrl_common + '/MBSD_IFP_UM/1200/0105',
				data: {
					phone: _this.phone,
					passwordArray: [{
						pwdType: 'LG',
						password: _this.password
					}],
					clientTime: new Date().getTime().toString(),
					loginIp: _this.loginIp,
				},
				method: 'POST',
				success: (res) => {
					uni.hideLoading();
					console.log(JSON.stringify(res));
					let retStatus = res.data.sysHead.retStatus;
					if (retStatus === 'S' && res.data.body !== null) {
						uni.reLaunch({
							url: 'pages/login/index/index'
						});
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.sysHead.ret[0].retMsg
						});
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: "手机号注册失败！"
					});
				}
			});
		}
	}
}
